  .HeaderCartButton_bump__1_4ZW {
    -webkit-animation: HeaderCartButton_bump__1_4ZW 300ms ease-out;
            animation: HeaderCartButton_bump__1_4ZW 300ms ease-out;
  }
  
  @-webkit-keyframes HeaderCartButton_bump__1_4ZW {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    10% {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
    }
    30% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
    50% {
      -webkit-transform: scale(1.15);
              transform: scale(1.15);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes HeaderCartButton_bump__1_4ZW {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    10% {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
    }
    30% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
    50% {
