.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8a2b06;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  
  
  .search {
    width: 30%;
  /* width: 100%; */
  margin-top: 20px;
  margin-bottom: 20px;
  color: white;
  
  padding: 20px;
  border: none;
  display: flex;
  border-radius: 10px;
  font-size: 20px;
  z-index: 0;
  overflow:hidden;
}

